import React from 'react';

export default class ListaDeAlumnos extends React.Component {
    constructor(props){
        super(props)
        this.state = {
            alumnos: props.alumnos,
            value: ""
        }
    }
    handleChange= (e) => {
        const value = e.target.value.toLowerCase();
            this.setState({value: value}, this.filtrar)
            if(e.keyCode === 8 || 229){
                console.log("borro")
                this.setState({alumnos: this.props.alumnos}, this.filtrar);
            }
    }    
    filtrar = () =>{
        const result = this.state.alumnos.filter(e => e.first_name.toLowerCase().includes(this.state.value) || e.last_name.toLowerCase().includes(this.state.value))
        this.setState({alumnos: result})    
    }
    reset = () => {
        this.setState({alumnos: this.props.alumnos}); 
        document.getElementById("buscar").value = "";
              
    }
    render(){
        return (
            <div className="alumnos">
                <h1>Alumnos: </h1>
                <input id="buscar" type="text" placeholder="Ingresa nombre aquí" onKeyUp={this.handleChange}/>
                <button onClick={this.reset}>RESETEAR LISTA</button>
                <div className="datos">
                   {this.state.alumnos.map(e => <Alumno key={e.id} alumnoData={e} />)} 
                </div>
            </div>
        )
    }
}
const Alumno = (props) =>{
    return (
        <div>
            <span>ID: {props.alumnoData.id} </span>
            <span>{props.alumnoData.last_name}, </span>
            <span className="name">{props.alumnoData.first_name}</span> 
            <span>DNI: {props.alumnoData.dni}</span>
        </div>
    )
} 