import React from 'react';

export default class Contador extends React.Component{
    constructor(){
        super()
        this.state = {
            valor:0
        }
/*     this.incrementar = this.incrementar.bind(this); */
    }
    //opcion: arrow functions
    incrementar = () =>{
        const logger = () => {
            console.log(this.state.valor);
        }
       this.setState({valor: this.state.valor + 1}, logger)
    }
    decrementar = () => {
        if(this.state.valor <= 0) return
        this.setState({valor: this.state.valor -1}) 
    }

/*     // opcion: funcion 
    increentar(){
        this.setState({valor: })
    } */
    evaluarClass = () =>{
        if (this.state.valor >= 0 && this.state.valor <= 5){
                return 'numeroBajo'
            }else if(this.state.valor >= 6 && this.state.valor <= 10){
                return 'numeroCorrecto'
            }else {
                return 'numeroAlto'
            }
        }

    render(){
        return(
            <div className="contador">
                <h2>Contador</h2>
                <h3>Mi valor es: <span className={this.evaluarClass(this.state.valor)}>{this.state.valor}</span></h3>
                <button onClick={this.incrementar}>INCREMENTAR</button>
                <button onClick={this.decrementar}>DECREMENTAR</button>
            </div>
        )
    }
};
